import { _decorator, Component, Node, instantiate, Prefab, Vec3,director } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('Entities')
export class Entities extends Component {

    @property(Prefab) 
    playerPrefab: Prefab;

    @property(Prefab)
    stickPrefab: Prefab;
    
    @property(Prefab)
    columnPrefab: Prefab;

    private _player: Node;
    private _stick: Node;
    private _startColumn: Node;
    private _nextColumn: Node;

    createAllEntities() {
        this._startColumn = instantiate(this.columnPrefab);
        this._nextColumn = instantiate(this.columnPrefab);
        this._player = instantiate(this.playerPrefab);
        this._stick = instantiate(this.stickPrefab);
    }

    getEntities(): Record<string, Node> {
        return {
            player: this._player,
            stick: this._stick,
            startColumn: this._startColumn,
            nextColumn: this._nextColumn,
        };
    }
    
    start() {

    }

    update(deltaTime: number) {
        
    }
}


